.wrapper{
     display: flex;
     justify-content: center;
}

.scene{
     width: 260px;
     height: 200px;
     position: relative;
     perspective: 1000px;
}

.scene .carousel.keen-slider {
     width: 100%;
     height: 100%;
     position: absolute;
     overflow: visible;
     transform: translateZ(-288px);
     transform-style: preserve-3d;
}

.carousel__cell{
     position: absolute;
     width: 240px;
     left: 10px;
     height: 200px;
     border: 1px solid rgba(0, 0, 0, 0.3);
}